/**
 * @class
 * @description     
 */
const Map = class Map {
    constructor() {
        if (document.querySelector('#map_yandex')) {
            this.create();
        }
    }
    /**
    * @description     
    * @param  {String}  [selector = map_yandex]  [id    ,  #]
    * @param  {Integer} [zoom = 16]     [Zoom    ]
    * @param  {Array}   [center = [55.76, 37.64]]   [    ]
    * @param  {Array}   [points = []]   [   .         [55.76, 37.64],      ]
    * @return {Promise}  map             [ ]
    * 
    * @example
    * //     
    * 
    * window.app.map.create({
    selector: 'footer__map',
    zoom: 10,
    center: [55.76, 37.64],
    points: [ ",  5", [55.76, 37.64] ]
})
    * @example
    * //        then(),    map
    * 
    * window.app.map.create({
    selector: 'footer__map',
    zoom: 10,
    center: [55.76, 37.64],
    points: [ ",  5", [55.76, 37.64] ]
}).then(map => {
    console.log(map);
    map.setZoom(18);
    //        ,     map
    //      : {@link https://yandex.ru/dev/jsapi30/doc/ru/ref/#class-ymap}
})

    */
    create(
        {
            selector = 'map_yandex',
            center = [55.76, 37.64],
            zoom = 16,
            controls = ['default'],
            points = []
        } = {}
    ) {

        return new Promise((resolve, reject) => {
            if (!document.querySelector(`#${selector}`)) {
                reject(`      ${selector}`);
                return false;
            }

            ymaps.ready( () => {

                let map = new ymaps.Map(selector, {
                    center,
                    zoom,
                    controls
                });

                if (points) {
                    this.setMapPoint(map, points);
                }

                resolve(map);       
            })
        })

        
    }

    async setMapPoint(map, points) {
        await points.forEach(point => {
            ymaps.geocode(point, {results:1}).then((res) => {
                let coords = [res.geoObjects.get(0).geometry.getCoordinates()[0], res.geoObjects.get(0).geometry.getCoordinates()[1]];
                console.log('res', res, coords);
                map.geoObjects.add(new ymaps.Placemark(coords, {}, {
                    draggable: true
                }));
                map.setCenter(coords);
            });
        });

        
        
    }
}

export default Map;